class Home extends React.Component {

  componentDidMount(){
    function getTimeRemaining(endtime) {
      var t = Date.parse(endtime) - Date.parse(new Date());
      var seconds = Math.floor((t / 1000) % 60);
      var minutes = Math.floor((t / 1000 / 60) % 60);
      var hours = Math.floor((t / (1000 * 60 * 60)) % 24);
      var days = Math.floor(t / (1000 * 60 * 60 * 24));
      return {
        'total': t,
        'days': days,
        'hours': hours,
        'minutes': minutes,
        'seconds': seconds
      };
    }

    function initializeClock(id, endtime) {
      var clock = document.getElementById(id);
      var daysSpan = clock.querySelector('.days');
      var hoursSpan = clock.querySelector('.hours');
      var minutesSpan = clock.querySelector('.minutes');
      var secondsSpan = clock.querySelector('.seconds');

      function updateClock() {
        var t = getTimeRemaining(endtime);

        daysSpan.innerHTML = t.days;
        hoursSpan.innerHTML = ('0' + t.hours).slice(-2);
        minutesSpan.innerHTML = ('0' + t.minutes).slice(-2);
        secondsSpan.innerHTML = ('0' + t.seconds).slice(-2);

        if (t.total <= 0) {
          clearInterval(timeinterval);
        }
      }

      updateClock();
      var timeinterval = setInterval(updateClock, 1000);
    }

    var deadline = new Date(Date.parse(new Date()) + 15 * 24 * 60 * 60 * 1000);
    initializeClock('clockdiv', deadline);
  }

  render() {
    return (
      <div>
      {/* Block t hng */}
      <br />
      <h2 className="text-uppercase block_title text-center w_100">THNG TIN CNG TY</h2>
      <br/>
      <h3 style={{textAlign: 'center', fontWeight: 'bolder'}}>CNG TY C PHN DC PHM GINIC</h3>
      <p style={{textAlign: 'center', fontWeight: 'bolder'}}>S 13, Lin k 10, KT Vn Ph P.Ph La, Q.H ng</p>
      <p style={{textAlign: 'center', fontWeight: 'bolder'}}>T: 0965916288</p>
      <p style={{textAlign: 'center', fontWeight: 'bolder'}}>GPKKD: 0106719468 - Cp ngy 03 thng 04 nm 2015</p>

      <div className="block_dathang" style={{marginTop: 50}}>
        <div className="container">
          <div className="text_giamgia">
            t hng ngay<br />
            <strong> C GIM</strong><br />
            <span>350.000 <b></b></span>
          </div>
          <div className="giatri_giam">
            <div className="percent_giam">11.4%</div>
            <div className="tien_giam">350.000 <b></b></div>
          </div>
          <div id="clockdiv" className="clockdiv col-lg-12 col-md-12 col-sm-6 no-gutter">
            <label>Cn li: </label>
            <div style={{display: 'none'}}>
              <span className="days">14</span>
              <div className="smalltext">Ngy</div>
            </div>
            <div>
              <span className="hours">23</span>
              <div className="smalltext">Gi</div>
            </div>
            <div>
              <span className="minutes">59</span>
              <div className="smalltext">Pht</div>
            </div>
            <div>
              <span className="seconds">37</span>
              <div className="smalltext">Giy</div>
            </div>
          </div>
          <div className="col-md-12 col-xs-12">
            <form className="form_dathang">
              <div className="col-md-6 col-sm-12 col-xs-12 f_item">
                <input type="text" className="form-control" placeholder="H v Tn" />
              </div>
              <div className="col-md-6 col-sm-12 col-xs-12 f_item">
                <input type="text" className="form-control" placeholder="a ch giao hng" />
              </div>
              <div className="col-md-6 col-sm-12 col-xs-12 f_item">
                <input type="text" className="form-control" placeholder="S in thoi" />
              </div>
              <div className="col-md-6 col-sm-12 col-xs-12 f_item">
                <input type="text" className="form-control" placeholder="Email" />
              </div>
              {/* <div className="col-md-6 col-sm-12 col-xs-12 f_item">
                <select className="form-control">
                  <option value={1}>Sn phm</option>
                  <option value={2}>2</option>
                </select>
              </div>
              <div className="col-md-6 col-sm-12 col-xs-12 f_item">
                <select className="form-control">
                  <option value={1}>S lng</option>
                  <option value={2}>2</option>
                </select>
              </div> */}
              {/* <div className="tongtien">
                <label className="pull-right">Tng tin</label>
                <span className="total_money pull-right">750.000 <b></b></span>
              </div> */}
            </form>
            <div className="col-md-12 text-center col-sm-12 col-xs-12 f_item">
              <button className="btn_dhngay">t hng ngay</button>
            </div>
          </div>
          <div className="hinhanh_sp">
            <img src="images/hinhanh_sp.png" alt />
          </div>
          <div className="icon_th">
            <img src="images/icon_th.png" alt />
          </div>
        </div>
      </div>

    </div>    
  );
  }
}

export default Home



